html, body {
  width: 100%;
  height: 100%;
  background-color: #0A0D1D;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  font-family: sans-serif;
  text-align: center;
  background-color: #D6D6D6;
  width: 450px;
  height: 620px;
  border-radius: 50px;
}

input {
  margin-right: 1rem;
  margin-top: 1rem;
  width: 250px;
  height: 20px;
  border-radius: 10px;
  font-family: 'Montserrat', sans-serif;
  background-color: #0A0D1D;
  color: white;
}

h1 {
  margin: 0;
  padding-top: 2rem;
  font-family: 'Montserrat', sans-serif;
}

button {
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  background-color: #0077cc;
  color: white;
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
}

button:hover {
  background-color: #005ea3;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-top: 2rem;
}

li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background-color: #0A0D1D;
  margin-bottom: 0.5rem;
  border-radius: 0.25rem;
  border-radius: 10px;
  margin-left: .5rem;
  margin-right: .5rem;
  color: white;
  font-family: 'Montserrat', sans-serif;
  margin-top: .5rem;

}

li button {
  background-color: #ff6666;
  color: white;
}

li button:hover {
  background-color: #cc0000;
}

.btnMark {
  background-color: #fffc66;
  color: black;
  font-weight: normal;
}

.btnMark:hover {
  background-color: #dad614;
}

.btnDelete {  
  margin-right: .5rem;
  font-weight: normal;
}

.completed {
  background-color: #0a0d1d23;
}

.todo{
  word-break: break-all;
  max-width: 15rem;
  text-align: left;
}

h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
  padding-top: 2rem;
}